.spinner-container {
    display        : flex;
    justify-content: center;
    align-items    : center;
    height         : 100vh;
    /* t chiu cao ty theo  mun */
}

.spinner {
    width           : 50px;
    /* t kch thc ca vng trn */
    height          : 50px;
    border-radius   : 50%;
    border          : 3px solid #ccc;
    border-top-color: #333;
    animation       : spin 1s infinite linear;
    /* Thit lp animation vng quay */
}

@keyframes spin {
    to {
        transform: rotate(360deg);
        /* Quay vng trn 360  */
    }
}